@import "../../shared/styles/common.scss";

.modalMainContainerOverlay {
    position: fixed;
    top: 80px;
    left: 0;
    width: 100vw;
    height: 100vh;
    @include flex(center, center);
    background-color: rgba(0, 0, 0, 0.25);
    z-index: 1000;

    @include tablet {
      top: 0;
      background-color: rgba(33, 33, 33, 0.12);
    }
  }
  
  .modalMainContainer {
    position: relative;
    top: 0;
    background-color: getColor("bg-primary");
    width: 100%;
    height: 100vh;
    @include tablet {
      top: 0;
      width: 594px;
    }
    @include desktop {
      width: 690px;  
    }
  }
  
  .container {
    padding: 40px 15px 0 15px;
    @include tablet {
      padding: 64px 42px 0 42px;
    }
    @include desktop {
      padding: 64px 90px 0 90px;
    }
  }
  
  .mobileBackground {
    position: relative;
    width: 100%;
    height: 40px;
    background: getColor("bg-primary");
    @include tablet {
      display: none;
    }
  }
  
  .closeModalBtnMobile {
    background-image: url('./closeM.svg');
    background-color:transparent, getColor("bg-primary");
    background-repeat: no-repeat;
    position: absolute;
    top: 18px;
    left: 24px;
    width: 30px;
    height: 24px;
    border: none;
    cursor: pointer;
  }
  
  .closeModalBtn {
    background-image: url('./close.svg');
    background-color:transparent, getColor("bg-primary");
    background-repeat: no-repeat;
    display: none;
    position: absolute;
    top: 24px;
    right: 24px;
    width: 12px;
    height: 12px;
    border: none;
    cursor: pointer;
    @include tablet {
      display: block;
    }
  }
  
 